@import "index"
@import "layout"


body
  -webkit-transform: translate3d(0, 0, 0)

.first
  background: url('../images/bg.png') no-repeat center center
  background-size: cover
  height: 100vh
  min-height: Rem(600)
  .container
    @extend .column
    color: #fff
    justify-content: space-between

    .navbar
      @extend .row
      justify-content: space-between


      &-logo
        padding-top: Rem(31)
        display: flex
        justify-content: flex-start
        align-items: flex-start

      &-list
        +light(14,22)
        padding-top: Rem(41)
        -webkit-font-smoothing: initial
        @extend .row

        &-item
          padding: 0 Rem(27)
          
          & > a
            color: #fff
            position: relative
            padding-bottom: Rem(15)
            &:after
              position: absolute
              content: ''
              left: 50%
              transform: translate3d(-50%,0,0)
              height: Rem(2)
              background-color: #fff
              width: 0
              bottom: 0
              transition: width 0.3s ease

            &:hover
              &:after
                width: 100%

      &-call
        +light(14,22)
        -webkit-font-smoothing: initial
        padding-top: Rem(41)
        text-align: right

        a
          line-height: Rem(50)
          text-decoration: underline
          color: #fff


    .headers
      @extend .row
      text-align: left
      align-items: center
      flex: 1 0 auto

      &-item
        flex: 1 0 auto
        max-width: 50%
        +medium(60,60)
        -webkit-font-smoothing: initial

        .menu
          @extend .row
          justify-content: flex-start

          &-item
            padding: 0 Rem(50) Rem(60) 0
            +light(14,20)
            -webkit-font-smoothing: initial
            cursor: pointer
            a
              color: #fff
              outline: none
            &.active
              a
                text-decoration: underline


        .gallery

          &-item
            +light(24,35)
            -webkit-font-smoothing: initial
            outline: none


          .slick-dots 
            text-align: left
            margin-bottom: Rem(-45)
            li 
              width: Rem(76)
              margin: 0
              button
                width: 100%
                &:before
                  content: ''
                  height: 5px 
                  width: 100%
                  background: #fff



    .scrolldown
      flex: 1 0 
      max-height: 10rem
      @extend .column
      justify-content: flex-start
      align-items: center

      img
        margin-top: Rem(35)

.second
  .container
    padding-top: Rem(70)
    padding-bottom: Rem(70)
    @extend .column
    text-align: left


    .title
      margin-top: Rem(25)

    .text
      letter-spacing: 0
      margin-top: Rem(40)
      max-width: Rem(1081)
      span
        display: block



.our-products
  .container
    @extend .column
    text-align: left

    .title
      margin-top: Rem(25)
      letter-spacing: Rem(2)


    .products
      margin-top: Rem(30)
      @extend .row
      flex-wrap: wrap
      justify-content: space-between



      .product-1
        background: url('../images/product-1.png') no-repeat center center
        background-size: cover

      .product-2
        background: url('../images/product-2.png') no-repeat center center
        background-size: cover

      .product-3
        background: url('../images/product-3.png') no-repeat center center
        background-size: cover

      .product-4
        background: url('../images/product-4.png') no-repeat center center
        background-size: cover

      .product
        flex: 1 0 auto
        max-width: 49.5%
        position: relative
        min-height: Rem(300)
        margin-top: 1%
        +tablet
          max-width: 100%
          flex: 1 0 100%

        +mobile
          max-width: 100%
          flex: 1 0 100%

        +normal
          max-width: 100%
          flex: 1 0 100%

        &:hover
          .products-hover
            opacity: 1

          .product-title
            opacity: 0.01

        &-title
          opacity: 1
          transition: opacity 0.3s ease
          color: #fff
          -webkit-backface-visibility: hidden
          +light(24,26)
          padding: Rem(35) Rem(35)
          -webkit-font-smoothing: initial


        .products-hover
          left: 0
          top: 0
          width: 100%
          height: 100%
          background: rgba(255,255,255,.75)
          opacity: 0.01
          transition: opacity 0.3s ease
          position: absolute
          -webkit-backface-visibility: hidden
          -webkit-transform-style: preserve-3d

          &-title
            +light(24,26)
            color: $bluebox
            padding: Rem(35) Rem(35) Rem(0) Rem(35)
            -webkit-font-smoothing: initial

          &-text
            padding: Rem(17) Rem(35)
            -webkit-font-smoothing: initial 
            +light(15,20)
            color: $bluebox

.service
  .container
    padding-top: Rem(70)
    padding-bottom: Rem(70)
    @extend .column
    text-align: left

    .title
      margin-top: Rem(25)

    .text
      letter-spacing: 0
      margin-top: Rem(40)
      max-width: Rem(899)


    .services
      @extend .row
      flex-wrap: wrap
      justify-content: space-between

      &-item
        margin-top: Rem(61)
        flex: 1 0 26%
        max-width: 26%
        +mobile
          flex: 1 0 55%
          max-width: 48%


        &-title
          +light(15,20)
          margin-top: Rem(34)
          max-width: Rem(250)


.info-slider
  background: url('../images/bg3.png') no-repeat center center
  background-size: cover

  .container
    text-align: left
    color: #fff
    padding-top: Rem(72)
    padding-bottom: Rem(118)

    .slider
      @extend .column

      &-item
        outline: none
        &-title
          +light(48,46)

        &-text
          padding-top: Rem(46)
          +light(24,36)

      .slick-dots 
        text-align: left
        bottom: -75px
        li 
          width: Rem(76)
          margin: 0
          button
            width: 100%
            &:before
              content: ''
              height: 5px 
              width: 100%
              background: #fff
.info
  .container
    padding-top: Rem(70)
    @extend .column
    text-align: left
    justify-content: space-between

    .title
      margin-top: Rem(25)
      letter-spacing: Rem(2)

    

    .info-container
      margin-top: Rem(60)
      @extend .row
      justify-content: space-between

      .info-left
        flex: 1 0 auto
        min-height: Rem(431)
        max-height: Rem(431)
        max-width: 45%
        @extend .column
        justify-content: space-between


        &-item
          cursor: pointer
          @extend .row
          justify-content: space-between
          align-items: flex-start
          

          &-image
            flex: 1 0 auto
            opacity: 0.3
            img
              filter: grayscale(100%)

          &-text
            margin-left: Rem(49)
            @extend .column

            & > .title
              margin-top: initial
              +regular(24,26)
              -webkit-font-smoothing: initial

            & > .description
              margin-top: Rem(17)
              +light(15,20)


      .info-right
        flex: 1 0 auto
        max-width: 45%
        +light(22,36)
        letter-spacing: 0
        color: $bluebox
        .info-right-text
          display: none

    .info-right-active
      display: flex !important

    .info-active
      .info-left-item-text
        color: $bluebox
        .title
          color: $bluebox

      .info-left-item-image
        opacity: 1 !important
        img
          filter: none !important

.partner
  .container
    @extend .column
    padding-top: Rem(70)
    padding-bottom: Rem(70)
    text-align: left

    .title
      margin-top: Rem(25)
      letter-spacing: Rem(2)


    .partners
      margin-top: Rem(10)
      @extend .row
      flex-wrap: wrap
      justify-content: space-between

      &-item
        flex: 1 0 25%
        padding: Rem(50) Rem(0) Rem(50)
        display: flex
        flex-flow: column nowrap
        align-items: center
        justify-content: center


.write
  background: url('../images/bg2.png') no-repeat  center center
  background-size: cover

  .container
    padding-top: Rem(60)
    padding-bottom: Rem(70)
    text-align: left

    .hr
      background: #fff

    .title
      margin-top: Rem(25)
      letter-spacing: Rem(2)
      color: #fff
      -webkit-font-smoothing: initial


    .form
      margin-top: Rem(60)
      @extend .row
      justify-content: space-around

      .form-item
        @extend .column
        justify-content: space-between
        flex: 1 0 auto
        max-width: 34%
        &:last-child
          max-width: 55%

        label
          @extend .column
          color: #fff
          +light(15,20)
          -webkit-font-smoothing: initial

          input 
            margin-top: Rem(10)
            width: Rem(330)
            border-radius: 9px
            padding: Rem(16.5)
            outline: none
            border: none
            background: rgba(255,255,255,0.65)

          textarea
            margin-top: Rem(10)
            min-height: Rem(159)
            border-radius: 9px
            padding: Rem(16.5)
            outline: none
            border: none
            background: rgba(255,255,255,0.65)
            width: 100%

        input[type='submit']
          margin-top: Rem(60)
          padding: 1.35rem 0
          height: Rem(60)
          background: transparent
          color: #fff
          outline: none
          border: 2px #E6E6E6 solid
          +light(15,20)
          -webkit-font-smoothing: initial


.contact
  .container
    @extend .column
    text-align: left
    padding-top: Rem(60)
    padding-bottom: Rem(60)

    .title
      margin-top: Rem(25)
      letter-spacing: Rem(2)
      -webkit-font-smoothing: initial

    .contacts
      @extend .row
      margin-top: Rem(60)

      &-item
        width: 20%

        &-title
          +light(15,20)
          margin-bottom: Rem(33)


        &-text
          +regular(15,35)
          a
            color: #000



.footer
  background: #3C3C3C
  .container
    text-align: left
    color: #fff
    +light(12,20)
    -webkit-font-smoothing: initial
    padding-top: Rem(11) 
    padding-bottom: Rem(11)